import { h, Component } from 'preact';
import HTML from 'components/core/html/html';

import s from 'base/browserNotSupported/browserNotSupportedApp.sss';

export default class BrowserNotSupportedApp extends Component {

	render(props) {
		const { initialStore } = props;

		const {
			noSupportWarning,
			noSupportCta,
			sharedImages
		} = initialStore || {};

		const chromeIcon = `${sharedImages}/icons/bolt/chromeIcon.svg`;
		const firefoxIcon = `${sharedImages}/icons/bolt/firefoxIcon.svg`;
		const edgeIcon = `${sharedImages}/icons/bolt/edgeIcon.png`;
		const safariIcon = `${sharedImages}/icons/bolt/safariIcon.svg`;

		return (
			<div className={s.wrapper}>
				<div className={s.container}>
					<HTML markdown tag='h2'>{noSupportWarning}</HTML>
					<HTML markdown tag='h3'>{noSupportCta}</HTML>
					<div className={s.icons}>
						<a target='_blank' rel='noopener noreferrer' href='https://www.google.com/chrome/'><img alt='chromeIcon' src={chromeIcon}/></a>
						<a target='_blank' rel='noopener noreferrer' href='https://www.mozilla.org/en-GB/firefox/new/'><img alt='firefoxIcon' src={firefoxIcon}/></a>
						<a target='_blank' rel='noopener noreferrer' href='https://support.apple.com/en_GB/downloads/safari'><img alt='safariIcon' src={safariIcon}/></a>
						<a target='_blank' rel='noopener noreferrer' href='https://www.microsoft.com/en-gb/windows/microsoft-edge'><img alt='edgeIcon' src={edgeIcon}/></a>
					</div>
				</div>
			</div>
		);
	}
}
