import { h } from 'preact';
import { accessNested } from 'utils/utils';
import BrowserNotSupportedApp from 'base/browserNotSupported/browserNotSupportedApp';

export default function BrowserNotSupportedDoc(props) {

	const {
		relativeRoot = '/',
		reload,
		appData,
		appProps
	} = props;

	const { doc } = appData;

	const buildDate = Date.now();
	const pageTitle = accessNested(appData, 'meta.title.locale');
	const imageRegion = accessNested(appData, 'urls.roots.image');
	const { title, metaDescription } = doc;

	const faviconFolder = accessNested(appData, 'options.regionalFavicon.imageRegion', imageRegion);
	const faviconIco = `${faviconFolder}/favicon.ico?v=${buildDate}`;
	const faviconPng = `${faviconFolder}/favico.png?v=${buildDate}`;

	return (
		<html lang="en">
			<head>
				<meta charset="UTF-8" />
				<meta http-equiv="Content-Type" content="text/html; charset=UTF-8" />
				<title>{pageTitle}</title>
				<meta name="description" content={metaDescription} />
				<link rel="stylesheet" type="text/css" href={`${relativeRoot}css/browserNotSupported.css?v=${buildDate}`} />
				<link rel="stylesheet" href="https://use.typekit.net/gsb3nxg.css" />

				<meta name="author" content="POWSTER showtimes@powster.com" />
				<meta name="copyright" content={new Date().getFullYear()} />
				<meta name="viewport" content="minimal-ui, initial-scale=1" />

				<link rel="shortcut icon" type="image/png" href={faviconPng} />
				<link rel="icon" type="image/x-icon" href={faviconIco} />
			</head>
			<body>
				<div id="app">
					<BrowserNotSupportedApp {...appProps} />
				</div>
				{reload}
			</body>
		</html>
	);

}
